:root {
  --spacing: 8px;
}

body {
  line-height: 1.5;
}

.container {
  gap: calc(var(--spacing) * 3);
  padding: calc(var(--spacing) * 2);
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

.search-form {
  gap: var(--spacing);
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.search-form > button {
  padding: var(--spacing) calc(var(--spacing) * 2);
  text-transform: uppercase;
  cursor: pointer;
}

.search-form > input {
  min-width: 240px;
  max-width: 320px;
  padding: var(--spacing);
  font-size: 24px;
}

.search-form > input:focus {
  outline-color: #7fffd4;
}

.photo-card {
  background-color: #fff;
  border: 1px solid #eee;
  border-radius: 4px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.info {
  justify-content: space-around;
  display: flex;
}

.gallery {
  gap: calc(var(--spacing) * 2);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  display: grid;
}

.gallery img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}

.load-more {
  margin-left: auto;
  margin-right: auto;
  display: none;
}

.load-more.is-visible {
  display: inline-block;
}

